import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  Optional,
  Output,
  ViewChild
} from '@angular/core';
import { ControlValueAccessor, NgControl } from '@angular/forms';
import { FormComponent } from '@sofico-framework/ui-kit/components/form';
import {
  OnSofFocus,
  SOF_FOCUS_COMPONENT
} from '@sofico-framework/ui-kit/directives/focus';

let uniqueRadioCounter = 0;

@Component({
  selector: 'sof-input-radio',
  template: `
    <div class="custom-control custom-radio">
      <input
        #inputElement
        type="radio"
        [attr.id]="labelForId"
        [value]="value"
        [disabled]="isDisabled"
        [attr.name]="name"
        [checked]="value === ngControl?.value || checked"
        (change)="onChange(value)"
        [class.is-invalid]="
          invalid ||
          (ngControl?.invalid && (ngControl?.touched || form?.submitted))
        "
        class="custom-control-input"
      />
      <label class="custom-control-label" [attr.for]="labelForId">
        <ng-content></ng-content>
      </label>
    </div>
  `,
  styleUrls: ['./input-radio.component.scss'],
  providers: [
    { provide: SOF_FOCUS_COMPONENT, useExisting: InputRadioComponent }
  ]
})
export class InputRadioComponent implements ControlValueAccessor, OnSofFocus {
  /**
   * Determines the value of the control.
   */
  @Input() value: any;

  /**
   * Determines the name of the control.
   */
  @Input() name: string;

  /**
   * Determines whether the control is checked or not.
   */
  @Input() checked: boolean;

  /**
   *  Determines if the input is disabled.
   */
  @Input() isDisabled: boolean;

  /**
   * Determines whether the input is in a valid state.
   */
  @Input() invalid: boolean;

  /**
   * EventEmitter that will emit the value when changed.
   */
  @Output() changeValue = new EventEmitter<any>();

  @ViewChild('inputElement') inputElement: ElementRef;

  labelForId = 'sof-input-radio-' + uniqueRadioCounter; // generate unique id

  changeFn;
  touchFn;

  constructor(
    @Optional() public form: FormComponent,
    @Optional() public ngControl: NgControl
  ) {
    ++uniqueRadioCounter;
    if (ngControl) {
      ngControl.valueAccessor = this as any;
    }
  }

  sofFocus(): void {
    this.inputElement.nativeElement.focus();
  }

  onChange(e: any): void {
    if (this.changeFn) {
      this.changeFn(e);
    }
    this.changeValue.emit(e);
  }

  writeValue(obj: any): void {}

  registerOnChange(fn: any): void {
    this.changeFn = fn;
  }

  registerOnTouched(fn: any): void {
    this.touchFn = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }
}
